import React from "react"
import Link from "gatsby-link"

export default ({ data }) => {
    const news = data.markdownRemark;
    
    return (
        <div>
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <h1>
                            {news.frontmatter.title} {" "}
                            <small>{news.frontmatter.date}</small>
                        </h1>
                        <h2>{news.frontmatter.desc}</h2>
                        <h5>Posted By: {news.frontmatter.author}</h5>
                        <hr />
                        <div dangerouslySetInnerHTML={{__html: news.html}} />
                    </div>
                    <div className="col-lg-12">
                        <Link className="btn btn-primary" to="/news" role="button">&laquo; Return to News</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export const query = graphql`
    query NewPostQuery($path: String!) {
        markdownRemark(frontmatter: {path: {eq: $path}}) {
            html
            frontmatter {
                title
                date(formatString: "MMMM DD, YYYY")
                author
                desc
            }
        }
    }
`


